import xs from 'xstream';
import { div, button } from '@cycle/dom';

function intent(domSource) {
  return {
    toSearching: domSource.select('.button').events('click'),
  };
}

function model(actions) {
  let toSearch = false;
  return actions.toSearching.startWith(null)
    .map(() => {
      toSearch = !toSearch;
      return toSearch;
    });
}

function view(state$) {
  return state$.map(toSearching => (
    toSearching ? div([
      button('.button', 'is to Searching'),
    ]) : div([
      button('.button', 'not to Searching'),
    ])
  ));
}

export default function Header(sources) {
  const actions = intent(sources.DOM);
  const state$ = model(actions);
  const vdom$ = view(state$);

  return { DOM: vdom$ };
}



